import React from 'react';
// import  "./NewsList.css";

const NewsItem = ({title,description,url,urlToImage}) => {
    return (
        <>
        <div className="news_items">
            <img className="news_img" src={urlToImage} alt="NewsImg"/>
            <h3><a href={url}/>{title}</h3>
            <p>{description}</p>
        </div>
        </>
    )
}

export default NewsItem
